import React from "react";
import ReactDOM from "react-dom/client";
import { createHashRouter, RouterProvider } from "react-router-dom";
import { initializeIcons } from "@fluentui/react";

import './index.css';
import App from './App';  // Import the new App component

// Initialize Fluent UI icons
initializeIcons();

// Define the router (if you still want routing functionality)
const router = createHashRouter([
    {
        path: "/",
        element: <App />,  // Render the new App component here
        children: [
            {
                path: "*",
                lazy: () => import("./pages/NoPage"),  // Lazy load any missing pages
            },
        ],
    },
]);

// Render the app
ReactDOM.createRoot(document.getElementById("root") as HTMLElement).render(
    <React.StrictMode>
        <RouterProvider router={router} />
    </React.StrictMode>
);
