import { useRef, useState, useEffect } from "react";
import readNDJSONStream from "ndjson-readablestream";
import styles from "./Chat.module.css";
import { chatApi } from "../../api";
import { Answer, AnswerError, AnswerLoading } from "../../components/Answer";
import { QuestionInput } from "../../components/QuestionInput";
import { UserChatMessage } from "../../components/UserChatMessage";

const Chat = () => {
    const [pdfPage, setPdfPage] = useState<number>(0);
    const [pdfVisible, setPdfVisible] = useState<boolean>(false);
    const [isLoading, setIsLoading] = useState<boolean>(false);
    const [isStreaming, setIsStreaming] = useState<boolean>(false);
    const [error, setError] = useState<unknown>();
    const [selectedAnswer, setSelectedAnswer] = useState<number>(0);
    const [answers, setAnswers] = useState<[string, string][]>([]);
    const [streamedAnswers, setStreamedAnswers] = useState<[string, string][]>([]);
    const [history, setHistory] = useState<{ question: string; response: string }[]>([]);
    const [hasSentMessage, setHasSentMessage] = useState<boolean>(false);

    const lastQuestionRef = useRef<string>("");
    const chatMessageStreamEnd = useRef<HTMLDivElement | null>(null);

    const handleAsyncRequest = async (question: string, responseBody: ReadableStream<any>) => {
        let answer: string = "";

        const updateState = (newContent: string) => {
            return new Promise(resolve => {
                setTimeout(() => {
                    answer += newContent;
                    setStreamedAnswers(prev => [...prev, [question, answer]]);
                    resolve(null);
                }, 33);
            });
        };

        try {
            setIsStreaming(true);
            for await (const event of readNDJSONStream(responseBody)) {
                if (event["choices"] && event["choices"][0]["delta"]["content"]) {
                    setIsLoading(false);
                    await updateState(event["choices"][0]["delta"]["content"]);
                }
            }
        } finally {
            setIsStreaming(false);
        }

        return answer;
    };

    const makeApiRequest = async (question: string) => {
        lastQuestionRef.current = question;
        setError(undefined);
        setIsLoading(true);

        try {
            const request = { question, history };

            if (question === "FIRST BOT MESSAGE123") {
                chatApi(request);
                console.log("First bot message sent");
            } else {
                setHasSentMessage(true);
                const response = await chatApi(request);
                if (!response.body) throw new Error("No response body");

                if (isStreaming) {
                    const parsedResponse = await handleAsyncRequest(question, response.body);
                    setAnswers(prev => [...prev, [question, parsedResponse]]);
                } else {
                    const jsonResponse = await response.json();
                    const answer = jsonResponse[0].response;
                    const chatHistory = jsonResponse[0].chat_history || [];
                    console.log(answer);
                    setAnswers(prev => [...prev, [question, answer]]);
                    setHistory(chatHistory);
                }
            }
        } catch (e) {
            setError(e);
        } finally {
            setIsLoading(false);
        }
    };

    const togglePdf = (page: number) => {
        if (!pdfVisible) {
            setPdfPage(page);
            setPdfVisible(!pdfVisible);
        } else if (pdfPage === page) {
            setPdfVisible(!pdfVisible);
        }
    };

    useEffect(() => chatMessageStreamEnd.current?.scrollIntoView({ behavior: "smooth" }), [isLoading]);
    useEffect(() => chatMessageStreamEnd.current?.scrollIntoView({ behavior: "auto" }), [streamedAnswers]);
    useEffect(() => {
        makeApiRequest("FIRST BOT MESSAGE123");
    }, []);

    return (
        <div className={styles.splitContainer}>
            <div className={styles.chatSection}>
                <div className={styles.chatContainer}>
                    {!hasSentMessage ? (
                        <div className={styles.welcomeScreen}>
                            <h2 className={styles.welcomeTitle}>  '     !</h2>
                            <p className={styles.welcomeText}>
                                           2025. <br />
                                      .
                            </p>
                            <div className={styles.warningBox}>
                                <p>
                                       ,      .          
                                    .
                                </p>
                            </div>
                            <p className={styles.disclaimer}>
                                        2025.        '     
                                  ,   .      .
                            </p>
                            <div className={styles.space} />

                            {/* Input box */}
                            <div className={styles.chatInput}>
                                <QuestionInput
                                    placeholder="   ?"
                                    disabled={isLoading}
                                    onSend={data => makeApiRequest(data.question)}
                                    clearOnSend={true}
                                />
                            </div>

                            {/* Links */}
                            <div className={styles.links}>
                                <a href="#" className={styles.link}>
                                     
                                </a>{" "}
                                |
                                <a href="#" className={styles.link}>
                                       
                                </a>
                            </div>

                            {/* Buttons */}
                            <div className={styles.welcomeButtonsContainer}>
                                {["   ", "   ", " ", "  "].map((text, index) => (
                                    <button key={index} className={styles.welcomeButton}>
                                        {text}
                                        <span className={styles.questionMark}>?</span>
                                    </button>
                                ))}
                            </div>
                        </div>
                    ) : (
                        <div className={styles.chatMessageStream}>
                            {isStreaming &&
                                streamedAnswers.map((streamedAnswer, index) => (
                                    <div key={index}>
                                        <UserChatMessage message={streamedAnswer[0]} />
                                        <div className={styles.chatMessageGpt}>
                                            <Answer isStreaming={true} key={index} answer={streamedAnswer[1]} isSelected={false} showPDF={togglePdf} />
                                        </div>
                                    </div>
                                ))}
                            {!isStreaming &&
                                answers.map((answer, index) => (
                                    <div key={index}>
                                        <UserChatMessage message={answer[0]} />
                                        <div className={styles.chatMessageGpt}>
                                            <Answer
                                                isStreaming={false}
                                                key={index}
                                                answer={answer[1]}
                                                isSelected={selectedAnswer === index}
                                                showPDF={togglePdf}
                                            />
                                        </div>
                                    </div>
                                ))}
                            {isLoading && (
                                <>
                                    <UserChatMessage message={lastQuestionRef.current} />
                                    <div className={styles.chatMessageGptMinWidth}>
                                        <AnswerLoading />
                                    </div>
                                </>
                            )}
                            {error ? (
                                <>
                                    <UserChatMessage message={lastQuestionRef.current} />
                                    <div className={styles.chatMessageGptMinWidth}>
                                        <AnswerError error={error.toString()} onRetry={() => makeApiRequest(lastQuestionRef.current)} />
                                    </div>
                                </>
                            ) : null}
                            <div ref={chatMessageStreamEnd} />
                        </div>
                    )}
                    {hasSentMessage ? (
                        <div className={styles.chatInput}>
                            <QuestionInput
                                placeholder="   ?"
                                disabled={isLoading}
                                onSend={data => makeApiRequest(data.question)}
                                clearOnSend={true}
                            />
                        </div>
                    ) : null}
                </div>
            </div>

            {pdfVisible && (
                <div className={styles.pdfSection}>
                    <button className={styles.closePdfButton} onClick={() => setPdfVisible(false)} />
                    <iframe src={`./assets/work_plan.pdf#page=${pdfPage}&view=FitH`} title="PDF Viewer" className={styles.pdfViewer}></iframe>
                </div>
            )}
        </div>
    );
};

export default Chat;
