const BACKEND_URI = "https://pmo-chat-gcp-fixed-prompt-551167410568.us-central1.run.app";

export async function chatApi(request: { "question": string, history: object[] }): Promise<Response> {
    const body = {
        "message": request.question,
        "chat_history": request.history
    };
    return await fetch(BACKEND_URI, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(body)
    });
}