import { useEffect } from "react";
import { Stack } from "@fluentui/react";
import styles from "./Answer.module.css";

interface Props {
    answer: string;
    isSelected?: boolean;
    isStreaming: boolean;
    showFollowupQuestions?: boolean;
    showPDF: (pageNumber: number) => void;
}

// Function to format the response text into HTML
const formatText = (text: string, showPDF: (pageNumber: number) => void) => {
    // Convert **bold** to <strong>bold</strong>
    text = text.replace(/\*\*(.*?)\*\*/g, "<strong>$1</strong>");

    // Convert ( X) to clickable links with an onClick handler
    text = text.replace(/\( (\d+)\)/g, (match, pageNumber) => {
        return `<a class="link" style="color: blue;text-decoration: underline;cursor: pointer;" data-page="${pageNumber}">( ${pageNumber})</a>`;
    });

    const lines = text.split("\n");
    let result = "";
    let inList = false;

    lines.forEach(line => {
        line = line.trimEnd(); // Remove trailing spaces/tabs

        const match = line.match(/^[ \t]*\* (.*)/); // Detect bullets (ignore indentation)
        if (match) {
            const content = match[1];

            // Start list if not already inside one
            if (!inList) {
                result += "<ul>";
                inList = true;
            }

            result += `<li>${content}</li>`;
        } else {
            // Close the list before adding normal text
            if (inList) {
                result += "</ul>";
                inList = false;
            }

            result += `<p>${line}</p>`; // Normal paragraph
        }
    });

    // Close any remaining open list
    if (inList) {
        result += "</ul>";
    }

    return result;
};

export const Answer = ({ answer, isSelected, showPDF }: Props) => {
    // Use useEffect to attach event listeners after component mounts
    useEffect(() => {
        const links = document.querySelectorAll(".link");

        links.forEach(link => {
            link.addEventListener("click", (e) => {
                const pageNumber = parseInt(link.getAttribute("data-page") || "0", 10);
                showPDF(pageNumber);
            });
        });

        // Cleanup event listeners on unmount
        return () => {
            links.forEach(link => {
                link.removeEventListener("click", () => {});
            });
        };
    }, [showPDF]);

    return (
        <Stack className={`${styles.answerContainer} ${isSelected ? styles.selected : ""}`} verticalAlign="space-between">
            <Stack.Item>
                <Stack horizontal horizontalAlign="space-between"></Stack>
            </Stack.Item>

            <Stack.Item grow>
                <div
                    className={styles.answerText}
                    dangerouslySetInnerHTML={{ __html: formatText(answer, showPDF) }}
                ></div>
            </Stack.Item>
        </Stack>
    );
};
